:root {
  --Red: hsl(0, 78%, 62%);
  --Cyan: hsl(180, 62%, 55%);
  --Orange: hsl(34, 97%, 64%);
  --Blue: hsl(212, 86%, 64%);
  --Dark-Blue: hsl(234, 12%, 34%);
  --Grayish-Blue: hsl(229, 6%, 66%);
  --Light-Gray: hsl(0, 0%, 98%);
}

body {
  font-family: "League Spartan", sans-serif;
  font-family: "Poppins", sans-serif;
}

html {
  box-sizing: border-box;
}

*,
*::after,
*::before {
  box-sizing: inherit;
}

body {
  margin: 0;
  padding: 0;
}

h1 {
  margin: 0;
  padding: 0;
}

.tech__grid {
  display: grid;
  grid-template-columns: 19.375rem;
  justify-content: center;
  align-content: center;
}
@media (min-width: 37.5em) {
  .tech__grid {
    grid-template-columns: 19.375rem 19.375rem;
    gap: 29px;
  }
}
@media (min-width: 75em) {
  .tech__grid {
    grid-template-columns: 22rem 22rem;
    grid-template-rows: repeat(5, 1fr);
    gap: 30px;
  }
}
.tech__header {
  text-align: center;
}
@media (min-width: 37.5em) {
  .tech__header {
    grid-column: span 2;
  }
}
@media (min-width: 75em) {
  .tech__header {
    grid-column: span 3;
  }
}
.tech__headline {
  font-weight: lighter;
  font-size: 1.125rem;
  color: var(--Dark-Blue);
}
@media (min-width: 75em) {
  .tech__headline {
    font-size: 1.875rem;
  }
}
.tech__headline-text {
  font-size: 0.875rem;
  color: var(--Grayish-Blue);
}
@media (min-width: 75em) {
  .tech__headline-text {
    font-size: 1.25rem;
  }
}
.tech__card {
  position: relative;
  min-width: 19.375rem;
  min-height: 13.75rem;
  border-top: 3px solid;
  border-radius: 5px;
  box-shadow: 10px 10px 10px rgba(84, 158, 242, 0.25);
  padding: 35px 29px 29px 30px;
  margin-bottom: 1.5625rem;
}
@media (min-width: 75em) {
  .tech__card {
    max-height: 15.75rem;
    max-width: 22rem;
  }
}
.tech__card h1 {
  font-size: 1.0625rem;
}
.tech__card p {
  font-size: 0.6875rem;
  color: var(--Grayish-Blue);
}
.tech__card img {
  position: absolute;
  width: 3.6875rem;
  height: 3.6875rem;
  right: 0;
  bottom: 0;
  translate: -40% -40%;
}
@media (min-width: 75em) {
  .tech__card img {
    width: 4.125rem;
    height: 4.125rem;
  }
}
.tech__card:hover {
  cursor: pointer;
  translate: 0 -20%;
  opacity: 0.9;
  transition: translate 250ms ease-in-out;
}

.bold {
  font-weight: 600;
}

.supervisor {
  border-top: 4px solid var(--Cyan);
}
@media (min-width: 75em) {
  .supervisor {
    grid-column: 1/2;
    grid-row: 3/5;
  }
}

.team {
  border-top: 4px solid var(--Red);
}
@media (min-width: 75em) {
  .team {
    grid-column: 2/3;
    grid-row: 2/4;
  }
}

.karma {
  border-top: 4px solid var(--Orange);
}
@media (min-width: 75em) {
  .karma {
    grid-column: 2/3;
    grid-row: 4/6;
  }
}

.calculator {
  border-top: 4px solid var(--Blue);
}
@media (min-width: 75em) {
  .calculator {
    grid-column: 3/4;
    grid-row: 3/5;
  }
}